import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-dialog-card--head-title';

const DialogCardHeadTitle = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </span>
));

DialogCardHeadTitle.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

DialogCardHeadTitle.displayName = 'DialogCardHeadTitle';

export default DialogCardHeadTitle;
