import React, { forwardRef, Fragment } from 'react';
import { bool, func, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';
import ReactAriaModal from 'react-aria-modal';
import DialogCard from '../dialog-card';

const CSS_PREFIX = 'vds-dialog';
const CSS_STATE = 'vds-state';

const Dialog = forwardRef(({
  children,
  className,
  css,
  open,
  type,
  ...remainingProps
}, ref) => (
  <Fragment>
    {
      open
        ? <ReactAriaModal {...remainingProps}>
          <div
            className={classnames(CSS_PREFIX, {
              [`${CSS_STATE}--show`]: true,
              [`${CSS_STATE}--${type}`]: Boolean(type)
            }, className)}
            css={css}
            ref={ref}
          >
            <DialogCard>
              {children}
            </DialogCard>
          </div>
        </ReactAriaModal>
        : undefined
    }
  </Fragment>
));

Dialog.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  css: func,

  /**
   * Show dialog
   */
  open: bool,

  /**
   * Dialog Type
   */
  type: oneOf(['error', 'success', 'warning'])
};

Dialog.displayName = 'Dialog';

export default Dialog;
